*{box-sizing: border-box;}
body {
    background: blueviolet;
}
.containerList {
    position: relative;
    top: 20px;
    width: 300px;
    margin: 0 auto;
    background: white;
    display: flex;
    &__itemMobx {
        display: flex;
        list-style-type: none;
    }
    &__deleteIcon {
        position: absolute;
        left: 140px;
    }
    &__title {
        color: red;
    }
    &__item {
        margin: 0 auto;
        width: 400px;
    }
    &__list {
        list-style-type: none;
        width: 300px;
        height: 35px;
        border: 1px solid whitesmoke;
        border-radius: 10px;
        margin-bottom: 10px;
        color: white;
    }
    &__link {
        margin-left: 0px;
    }
  

}
.containerTask {
    position: position;
    display: flex;
}
.containerTestMobx {
    position: absolute;
    top: 0px;
    width: 350px;
    height: 350px;
    &__buttonTestMobx {
        top: 200px;
        width: 150px;
        height: 100px;
        background: chartreuse;
    }
}
.containerTestRouter {
    display: flex;
    &__item {
        padding: 0px 150px;
        list-style-type: none;
    }
}
.containerBody {
    width: 500px;
    height: 500px;
    margin: 0 auto;
    background: red;
}
